import React, { useContext, useState } from 'react'
import { Modal, Form, Input, Row, Col, Button, Select, message, Table } from 'antd';
import { useMutation } from '@apollo/client';
import { ADD_USER_PAGE, DELETE_USER_PAGE } from '../../../graphql/auth';
import { roleColumn } from '../tableColumn/roleColumn';
import { pageColumn } from '../tableColumn/pageColumn';

const { Option } = Select;

export default function AddPage({ open, setOpen,userID, dataPage , setRefetch }) {
   
    const [addPage, { loading, error }] = useMutation(ADD_USER_PAGE, {
        onCompleted: ({ addPage }) => {
            if(addPage.success===true){
                setRefetch()
                message.success("បញ្ចូលបានជោគជ័យ")
            }else{
                message.success(addPage.message )
            }
        },
        onError: (error) => {
            message.error(error.message)
        }
    })

    const [deletePages, { loading: deleteLoading, error: deleteError }] = useMutation(DELETE_USER_PAGE, {
        onCompleted: ({ deletePages }) => {
            if(deletePages.success===true){
                setRefetch()
                message.success("លុបបានជោគជ័យ")
                setOpen(false)
                form.resetFields()
            }else{
                message.error(deletePages.message)
            }
            
        },
        onError:(error)=>{
            message.error(error.message)
        }
    })


    let [form] = Form.useForm()

    const onFinish = (values) => {
        // console.log('Success:', values);

        addPage({
            variables: {
                userId:userID,
                page:values.page
            }
        })

        setOpen(false)
        form.resetFields()
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const handleDelete = (e) => {
        deletePages({variables:{
            userId:userID,
            pageId:e
        }})
    }

    return (
        <Modal
            title="បញ្ចូលគេហទំព័រ"
            visible={open}
            onOk={() => setOpen(false)}
            onCancel={() => setOpen(false)}
            footer={null}
        >
            <Form
                form={form}
                name="addPage"
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Col xs={12}>
                        <Form.Item
                            name="page"
                            rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            {/* <Input placeholder="តួនាទី" /> */}
                            <Select placeholder="គេហទំព័រ" style={{ width: "100%" }}>
                                <Option value="/">ទំព័រដើម</Option>
                                <Option value="/sample">ការយកសំណាក</Option>
                                <Option value="/interview">អ្នកជំងឺ COVID-19</Option>
                                <Option value="/hospital">មណ្ឌលព្យាបាល</Option>
                                <Option value="/school">សាលារៀន</Option>
                                <Option value="/quarantine">មណ្ឌលចត្តាឡីស័ក</Option>
                                <Option value="/specifylocation">ទីតាំងបិទ/បើក</Option>
                                <Option value="/qrcode">QR Code</Option>
                                <Option value="/shop">ហាង់លក់</Option>
                                <Option value="/seller">អ្នក់លក់</Option>
                                <Option value="/buyer">អ្នក់ទិញ</Option>
                                <Option value="/people">ព័ត៌មានអ្នកសង្ស័យ</Option>
                                <Option value="/reportDaily">របាយការណ៍</Option>
                                <Option value="/reportExcel">របាយការណ៍ជា Excel</Option>
                                <Option value="/case">កំណត់ត្រាករណី</Option>
                                <Option value="/user">កំណត់អ្នកប្រើប្រាស់</Option>
                                <Option value="/finding">ស្វែងរកអ្នកជំងឺ</Option>
                                <Option value="/graph">ក្រាប</Option>
                                <Option value="/death">អ្នកស្លាប់</Option>
                            </Select>
                            
                        </Form.Item>
                    </Col>
                    <Col xs={{span: 11, offset:1}}>
                        <Button
                            htmlType="submit"
                            type="primary"
                            style={{ width: "100%" }}
                        // onClick={()=> }
                        >
                            បញ្ចូលទិន្នន័យ
                        </Button>
                    </Col>

                    <Col xs={24}>
                        <Table
                            columns={pageColumn({handleDelete})}
                            dataSource={dataPage}
                            pagination={false}
                        />
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
