import React, { Component } from 'react'
import Logo from '../../asset/srLogo.png'
import {Row, Col} from 'antd'
import {getKhmerDate} from '../../function/fn'

export default class ReportConfirm extends Component {
    render() {
        let personalInfo = this.props.data
        console.log(this.props.moreData)
        return (
            <Row
                style={{
                    padding: 50
                }}
            >
                <Col
                    xs={24}
                >
                    <div
                            style={{
                                textAlign: "center",
                                // width: 300,
                                // float: 'right'
                                // backgroundColor: "blue"
                            }}
                        >
                            <p>
                                <span className="HeaderFont">ព្រះរាជាណាចក្រកម្ពុជា<br />
                                    ជាតិ សាសនា ព្រះមហាក្សត្រ

                                </span><br />

                                <span className="HeaderFont1"
                                    style={{
                                        fontSize: "300pt !important"
                                    }}
                                >3</span>
                            </p>
                        </div>
                </Col>
                <Col
                    xs={24}
                >
                    <img src={Logo} width="130" /><br/>
                    <p>
                        មន្ទីរសុខាភិបាលនៃរដ្ឋបាលខេត្តសៀមរាប<br />
                        ការិយាល័យស្រុកប្រតិបត្តិអង្គរជុំ<br />
                        មន្ទីរពេទ្យបង្អែកពួក<br />
                        លេខ៖                   ម.ន.ព
                    </p>
                </Col>
                <Col
                    xs={24}
                >
                    <center>
                        <h3>
                            <b>
                                <u>
                                    លិខិតបញ្ជាក់
                                </u>
                            </b>
                        </h3>
                    </center>
                </Col>
                <Col
                    xs={24}
                    style={{
                        marginBottom: 50
                    }}
                >
                    <dl>
                        <dd>-&emsp;ឈ្មោះ៖ <b>{personalInfo?.lastName} {personalInfo?.firstName}</b> ភេទ <b>{personalInfo?.gender}</b> អាយុ <b>{personalInfo?.age} ឆ្នាំ</b></dd>
                        <dd>-&emsp;អាស័យដ្ឋាន៖ {personalInfo?.village === "ក្រៅសៀមរាប" ? "" : (<span>ភូមិ <b>{personalInfo?.village}</b> ឃុំ <b>{personalInfo?.commune}</b> ស្រុក <b>{personalInfo?.district}</b></span>)} ខេត្ត <b>{personalInfo?.province}</b></dd>
                        <dd>-&emsp;សញ្ជាតិ ៖ <b>{personalInfo?.nationality}</b> អត្តសញ្ញាណប័ណ្ណ៖ {personalInfo?.idCard === "" || personalInfo?.idCard === null ? <b>មិនបានយកមក</b> : <b>{personalInfo?.idCard}</b>}</dd>
                        <dd>-&emsp;ពេលវេលាចូលសម្រាក៖ <b>{getKhmerDate(personalInfo?.hospitalizations[0]?.date_in)}</b></dd>
                        <dd>-&emsp;រោគវិនិច្ឆ័យសម្រាកពេទ្យ៖ <b>{personalInfo?.hospitalizations[0]?.infection}</b></dd>
                        <dd>-&emsp;ពេលវេលាចេញ៖ <b>{getKhmerDate(personalInfo?.hospitalizations[0]?.date_out)}</b></dd>
                        <dd>-&emsp;ស្ថានភាពជម្ងឺ៖ <b>ជាសះស្បើយ</b></dd>
                        <dd>-&emsp;ដំបូន្មានគ្រូពេទ្យ៖ បន្តធ្វើចត្តាឡីស័កនៅផ្ទះចំនួន១៤ថ្ងៃទៀតរាល់មានបញ្ហាសុខភាពត្រូវត្រលប់ទៅរក
                                    មណ្ឌលព្យាបាល និង ចត្តឡីស័ក ដែលនៅក្បែរផ្ទះ។
                        </dd>
                    </dl>
                </Col>
                <Col
                    xs={24}
                    style={{
                        marginBottom: 50
                    }}
                >
                    <div
                        style={{
                            textAlign: "center",
                            width: 400,
                            float: 'right',
                            // backgroundColor: "blue"
                        }}
                    >
                        <p>
                            <span>
                                {/* ថ្ងៃ<b>អាទិត្យ៧កើត</b> ខែ<b>ស្រាពណ៍ឆ្នាំឆ្លូវ</b> ត្រីស័ក ព.ស <b>២៥៦៥</b><br />
                                ពួក,ថ្ងៃទី<b>១៥</b> ខែ <b>សីហា</b> ឆ្នាំ<b>២០២១</b><br /> */}
                                {this.props.moreData?.khmerDate}<br />
                                {this.props.moreData?.englishDate}<br />
                                <b>គ្រូពេទ្យព្យាបាល</b>
                            </span><br/>
                        </p>
                            <p
                                style={{
                                    marginTop: 100
                                }}
                            >
                                {this.props.moreData?.name}
                            </p>
                    </div>
                </Col>
                <Col
                    xs={24}
                >
                    <p>
                        បានពិនិត្យនិងឯកភាព<br />
                        <b>ប្រធានមន្ទីរពេទ្យបង្អែកពួក</b>
                    </p>
                </Col>
            </Row>
        )
    }
}
