import { Space } from "antd"
import { Popconfirm } from "antd"
import { DeleteOutlined } from "@ant-design/icons"

export const pageColumn = ({handleDelete}) => {

    var array = [
    {
        title: 'គេហទំព័រ',
        dataIndex: 'page',
        key: 'page',
        width: 20,
        render:(text,record) => {

            let pageName = ""
            switch(record.page){
                case '/sample':
                    pageName="ការយកសំណាក"
                    break;
                case '/interview':
                    pageName="អ្នកជំងឺ COVID-19"
                    break;
                case '/hospital':
                    pageName="មណ្ឌលព្យាបាល"
                    break;
                case '/quarantine':
                    pageName="មណ្ឌលចត្តាឡីស័ក"
                    break;
                case '/specifylocation':
                    pageName="ទីតាំងបិទ/បើក"
                    break;  
                case '/people':
                    pageName="ព័ត៌មានអ្នកសង្ស័យ"
                    break;   
                case '/reportDaily':
                    pageName="របាយការណ៍"
                    break;    
                case '/reportExcel':
                    pageName="របាយការណ៍ជា Excel"
                    break;
                case '/case':
                    pageName="កំណត់ត្រាករណី"
                    break;
                case '/user':
                    pageName="កំណត់អ្នកប្រើប្រាស់"
                    break;
                case '/seller':
                    pageName="អ្នកលក់"
                    break;
                case '/graph':
                    pageName="ក្រាប"
                    break;
                case '/shop':
                    pageName="ហាងលក់"
                    break;
                case '/buyer':
                    pageName="អ្នកទិញ"
                    break;
                case '/qrcode':
                    pageName="QR Code"
                    break;
                case '/finding':
                    pageName="ស្វែងរកអ្នកជំងឺ"
                    break;
                case '/death':
                    pageName="អ្នកស្លាប់"
                    break;
                default:
                    pageName="ទំព័រដើម"
                    break;
            }
        
        return (
            <span>
                {pageName}
            </span>
        )
        }
    },
    {
        dataIndex: 'action',
        key: 'action',
        width: 20,
        render:(text,record) => (
            <Space size="middle"
            >
                <Popconfirm
                        title="តើអ្នកពិតចង់លុបមែនឬទេ?"
                        onConfirm={() => { handleDelete(record.id) }}
                        okText="ចង់"
                        cancelText="មិនចង់"
                    >
                        <span className="link" style={{ color: "red" }}><DeleteOutlined /></span>
                </Popconfirm>
            </Space>
        )
    }
    ]

    return array
}