import { Space, Popconfirm,Button,Tooltip } from 'antd'
import {
    EditOutlined,
    DeleteOutlined,
    EyeOutlined,
    CopyOutlined,
    FileExcelOutlined,
    AuditOutlined,
    FundViewOutlined

} from '@ant-design/icons';
import { Link } from 'react-router-dom';
import moment from 'moment';

export const subHospitalCol = ({handleOpenEditCurrentState,handleDelete,hospitalName,hospitalId,handleEditSubHospital, limit,page, setOpenPrintConfirmFn}) => {
    var no = ((page-1) * limit)
    var array = [
        {
            title: 'ល.រ',
            dataIndex: 'no',
            key: 'no',
            width: 110,
            // render: (text, record, i) => (
            //     <Space size="middle">
            //         {no += 1}
            //     </Space>
            // ),
        },
        {
            title: 'ឈ្មោះ',
            dataIndex: 'name',
            key: 'name',
            width: 100,
            render: (text, record) => (
                <span>
                    {record?.lastName} {record?.firstName}
                </span>
            ),
        },
        {
            title: 'ភេទ',
            dataIndex: 'gender',
            key: 'gender',
            width: 50,
            render: (text, record) => (
                <span>
                    {record?.gender}
                </span>
            ),
        },
        {
            title: 'អាយុ',
            dataIndex: 'age',
            key: 'age',
            width: 40,
            render: (text, record) => (
                <span>
                    {record?.age}
                </span>
            ),
        },
        {
            title: 'លេខទូរស័ព្ទ',
            dataIndex: 'phone',
            key: 'phone',
            width: 60,
            render: (text, record) => (
                <span>
                    {record?.tel}
                </span>
            ),
        },

        {
            title: 'កាលបរិច្ឆេទចូល',
            dataIndex: 'date_in',
            key: 'date_in',
            width: 100,
            render: (text, record) => {
                let hospitalLength = record?.hospitalizations?.length
                let hospitalization = record?.hospitalizations[hospitalLength-1]
                return(
                <Space size="middle">
                    {hospitalization.date_in === null ? "គ្មាន": moment.parseZone(hospitalization.date_in).format("ថ្ងៃទីDD ខែMM ឆ្នាំYYYY")}
                    {/* {moment.parseZone(hospitalization?.date_in).format("ថ្ងៃទីDD ខែMM ឆ្នាំYYYY")} */}
                </Space>
            )
            }
        },

        {
            title: 'កាលបរិច្ឆេទចេញ',
            dataIndex: 'date_out',
            key: 'date_out',
            width: 100,
            render: (text, record) => {

                // let hospitalization = record?.hospitalizations?.filter(e=> e.hospitalName === hospitalName  )
                let hospitalLength = record?.hospitalizations?.length
                let hospitalization = record?.hospitalizations[hospitalLength-1]

                return(
                <span size="middle">
                    {hospitalization.date_out === null ? "គ្មាន": moment.parseZone(hospitalization.date_out).format("ថ្ងៃទីDD ខែMM ឆ្នាំYYYY")}
                </span>
                )
            },
        },

        {
            title: 'កាលបរិច្ឆេទតាមដាន',
            dataIndex: 'date_follow',
            key: 'date_follow',
            width: 100,
            render: (text, record) => {

                // let hospitalization = record?.hospitalizations?.filter(e=> e.hospitalName === hospitalName  )
                let hospitalLength = record?.hospitalizations?.length
                let hospitalization = record?.hospitalizations[hospitalLength-1]

                return(
                <span size="middle">
                    {hospitalization?.nextSampleTestDate === null ? "គ្មាន": moment.parseZone(hospitalization?.nextSampleTestDate).format("ថ្ងៃទីDD ខែMM ឆ្នាំYYYY")}
                  
                </span>
                )
            },
        },
      
        {
            title: 'អាសយដ្ឋាន',
            dataIndex: 'address',
            key: 'address',
            width: 150,
            render: (text, record) => (
                <span size="middle">
                    {(record?.village !== "ក្រៅសៀមរាប") && record?.village+","}
                    {record?.commune !== "ក្រៅសៀមរាប" && record?.commune+","}
                    {record?.district !== "ក្រៅសៀមរាប" && record?.district+","}
                    {record?.province && record?.province}
                </span>
            ),
        },

        {
            title: 'ចំនួនយកសំណាក',
            dataIndex: 'test_count',
            key: 'test_count',
            width: 100,
            render: (text, record) => (
                <span size="middle">
                    {record?.sampleTest?.length} លើក
                </span>
            ),
        },

        {
            title: 'ស្ថានភាព',
            dataIndex: 'status',
            key: 'status',
            width: 90,
            render: (text, record) => {

                let hospitalLength = record?.hospitalizations?.length
                let arr = record?.hospitalizations[hospitalLength-1]

                var dayOut = moment.parseZone(arr?.date_out).format("YYYY-MM-DD");
                var dayNow = moment.parseZone(new Date()).format("YYYY-MM-DD");

                let state ="កុំពុងព្យាបាល";
                let classN = "badge-purple"

                var dayNextTest = moment.parseZone(arr?.nextSampleTestDate).format("YYYY-MM-DD");

                if(arr.nextSampleTestDate!==null &&  dayNextTest == dayNow){
                    state = "ដល់ថ្ងៃតាមដាន"
                    classN= "badge-warning" 
                }

                if(arr.date_out!==null &&  dayOut < dayNow){
                    state = "បានចេញ​"
                    classN= "badge-success"
                }

                // if(arr.date_out!==null &&  dayNextTest == dayNow){
                //     state = "ដល់ថ្ងៃតាមដាន"
                //     classN= "badge-purple"
                // }

                return (
                    <span className={classN}>{state}</span>
                )
            }
         
        },
        // {
        //     title: 'លក្ខណៈពាក់ព័ន្ធ',
        //     dataIndex: 'relatedInfo',
        //     key: 'relatedInfo',
        //     width: 30,
        //     render: (text, record) => (
        //         <Space size="middle">
        //             {record?.personalInfo?.direct ? "ផ្ទាល់":"ប្រយោល"}
        //         </Space>
        //     ),
        // },
        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 120,
            align: 'center',
            render: (text, record) => {
                let hospitalization = record?.hospitalizations?.filter(e => e.hospitalInfo===hospitalId)
                // let hospitalization = record?.hospitalizations[]

                return(
                <Space size="middle">
                    {/* <CopyOutlined  style={{ color: "green" }}/> */}
                    <Tooltip title="កែប្រែការព្យាបាល">
                        <EditOutlined onClick={()=> {handleEditSubHospital({...hospitalization[hospitalization?.length - 1],personalInfoId:record.id})}} style={{ color: "green" }}/>
                    </Tooltip>

                    <Tooltip title="កែប្រែស្ថានភាពបច្ចុប្បន្ន">
                        <FundViewOutlined onClick={()=> {handleOpenEditCurrentState({...record?.currentState,personalInfoId:record.id})}} style={{ color: "green" }}/>
                    </Tooltip>

                    <Popconfirm
                        title="តើអ្នកពិតចង់លុបមែនឬទេ?"
                        onConfirm={ ()=>{handleDelete(hospitalization[hospitalization?.length - 1].id, record.id)}}
                        okText="ចង់"
                        cancelText="មិនចង់"
                    >
                        <Tooltip title="លុបការព្យាបាល">
                            <DeleteOutlined style={{ color: "red" }} />
                        </Tooltip>
                    </Popconfirm>
                    {record?.currentState?.recovered === true ?
                    <Tooltip title="បោះពុម្ភលិខិតជាសះស្បើយ">
                        <AuditOutlined onClick={()=> {setOpenPrintConfirmFn(record)}} className="link"/>
                    </Tooltip>
                    : null}
                    <Tooltip title="បង្ហាញព័ត៌មានលម្អិត">
                        <Link className="link" to={"/subPeople/" + record?.id}><EyeOutlined /></Link>
                    </Tooltip>
                </Space>
                )
            },
        }
    ]
    return array
}