import React, { useContext } from 'react'
import { Modal, Form, Input, Row, Col, Button,Select, message} from 'antd';
import { useMutation } from '@apollo/client';
import { REGISTER_USER } from '../../../graphql/auth';

const { Option } = Select;

export default function AddUser({ open, setOpen,setRefetch }) {
    let [form] = Form.useForm()
    const [registerUser,{loading,error}] = useMutation(REGISTER_USER,{
        onCompleted:({registerUser})=>{
            
            if(registerUser.success===true){
                setRefetch()
                message.success("បញ្ចូលបានជោគជ័យ")
                form.resetFields()
                setOpen(false)
            }else{
                message.error(registerUser.message)
            }
            
            
        },
        onError:(error)=>{
            message.error(error.message)
        }
    })

    const onFinish = (values) => {

        registerUser({
            variables:{
                username:values.username,
                password:values.password,
                firstname:values.firstname,
                lastname:values.lastname,
                email:values.email,
                role:values.role,
                tel:values.tel,
                page:values.page
            }
        })   
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    return (
        <Modal
            title="បញ្ចូលអ្នកប្រើប្រាស់ថ្មី"
            visible={open}
            onOk={() => setOpen(false)}
            onCancel={() => setOpen(false)}
            footer={null}
        >
            <Form
                form={form}
                name="addUser"
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="username"
                            rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <Input placeholder="ឈ្មោះសម្គាល់អ្នកប្រើប្រាស់" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="password"
                            rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <Input.Password placeholder="លេខកូដសម្ងាត់" style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="lastname"
                            rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <Input placeholder="គោត្តនាម" style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="firstname"
                            rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <Input placeholder="នាម" />
                        </Form.Item>
                    </Col>

                    

                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="role"
                            rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            {/* <Input placeholder="តួនាទី" /> */}
                            <Select placeholder="តួនាទី" style={{ width: "100%" }}>
                            <Option value="BASIC">BASIC</Option>
                            <Option value="ADMIN">ADMIN</Option>
                            <Option value="SUPPER">SUPPER</Option>
                            <Option value="DEVELOPER">DEVELOPER</Option>
                            <Option value="POLICE">POLICE</Option>
                            <Option value="OFFICER">OFFICER</Option>
                            <Option value="DOCTOR">DOCTOR</Option>
                            <Option value="QUARANTINECONTROLLER">QUARANTINECONTROLLER</Option>
                            <Option value="QUARANTINECONTROLLER">QUARANTINECONTROLLER</Option>
                                <Option value="DELETE_TRANSACTION">DELETE_TRANSACTION</Option>
                                <Option value="CREATE_TRANSACTION">CREATE_TRANSACTION</Option>
                                <Option value="UPDATE_TRANSACTION">UPDATE_TRANSACTION</Option>
                                <Option value="CREATE_SHOP">CREATE_SHOP</Option>
                                <Option value="DELETE_SHOP">DELETE_SHOP</Option>
                                <Option value="UPDATE_SHOP">UPDATE_SHOP</Option>
                                <Option value="CREATE_PERSONALINFO">CREATE_PERSONALINFO</Option>
                                <Option value="UPDATE_PERSONALINFO">UPDATE_PERSONALINFO</Option>
                                <Option value="DELETE_PERSONALINFO">DELETE_PERSONALINFO</Option>
                                <Option value="PRINT_QRCODE">PRINT_QRCODE</Option>
                                <Option value="VIEW_SHOP_DETAIL">VIEW_SHOP_DETAIL</Option>
                                <Option value="VIEW_BUYER_DETAIL">VIEW_BUYER_DETAIL</Option>
                                <Option value="VIEW_ALL_SHOP">VIEW_ALL_SHOP</Option>
                                <Option value="VIEW_SELLER">VIEW_SELLER</Option>
                                <Option value="VIEW_BUYER">VIEW_BUYER</Option>
                                <Option value="VIEW_ONE_SHOP">VIEW_ONE_SHOP</Option>
                                
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="page"
                            rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            {/* <Input placeholder="តួនាទី" /> */}
                            <Select placeholder="គេហទំព័រ" style={{ width: "100%" }}>
                                <Option value="/">ទំព័រដើម</Option>
                                <Option value="/sample">ការយកសំណាក</Option>
                                <Option value="/interview">អ្នកជំងឺ COVID-19</Option>
                                <Option value="/hospital">មណ្ឌលព្យាបាល</Option>
                                <Option value="/school">សាលារៀន</Option>
                                <Option value="/quarantine">មណ្ឌលចត្តាឡីស័ក</Option>
                                <Option value="/specifylocation">ទីតាំងបិទ/បើក</Option>
                                <Option value="/qrcode">QR Code</Option>
                                <Option value="/shop">ហាង់លក់</Option>
                                <Option value="/seller">អ្នក់លក់</Option>
                                <Option value="/buyer">អ្នក់ទិញ</Option>
                                <Option value="/people">ព័ត៌មានអ្នកសង្ស័យ</Option>
                                <Option value="/reportDaily">របាយការណ៍</Option>
                                <Option value="/reportExcel">របាយការណ៍ជា Excel</Option>
                                <Option value="/case">កំណត់ត្រាករណី</Option>
                                <Option value="/user">កំណត់អ្នកប្រើប្រាស់</Option>
                                <Option value="/finding">ស្វែងរកអ្នកជំងឺ</Option>
                                <Option value="/graph">ក្រាប</Option>
                                <Option value="/death">អ្នកស្លាប់</Option>
                            </Select>
                            
                        </Form.Item>
                    </Col>
                    
                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="tel"
                        // rules={[{ required: true, message: 'Please input your username!' }]}
                        >
                            <Input placeholder="លេខទូរស័ព្ទ" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }} >
                        <Form.Item
                            name="email"
                        // rules={[{ required: true, message: 'Please input your username!' }]}
                        >
                            <Input type="email" placeholder="អ៊ីម៉ែល" />
                        </Form.Item>
                    </Col>

                    <Col xs={24}>
                        <Button
                            htmlType="submit"
                            type="primary"
                            style={{ width: "100%" }}
                            // onClick={()=> }
                        >
                            បញ្ចូលទិន្នន័យ
                        </Button>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
