import { Space, Popconfirm,Tooltip } from 'antd'
import {
    EyeOutlined,
    DeleteOutlined,
    QrcodeOutlined
} from '@ant-design/icons';
import { AiOutlineShop } from 'react-icons/ai'
import {GiShop} from 'react-icons/gi'
import { Link } from 'react-router-dom';
import moment from 'moment';
import { getKhmerTime } from '../../../function/fn';

export const buyerPageCol = () => {

    var array = [
        {
            title: 'ល.រ',
            dataIndex: 'no',
            key: 'no',
            width: 110,
            // render: (text, record, i) => (
            //     <span>
            //         {no+= 1}
            //     </span>
            // ),
        },
        // {
        //     title: 'អត្តសញ្ញាណប័ណ្ណ',
        //     dataIndex: 'idCard',
        //     key: 'idCard',
        //     width: 130,
        // },
        
        {
            title: 'ឈ្មោះ',
            dataIndex: 'personalInfos',
            key: 'personalInfos',
            width: 130,
            render: (text, record) => (
                <span size="middle">
                    {
                        record?.lastName+" "+record?.firstName
                    }
                </span>
            ),
        },

        // {
        //     title: 'អាស័យដ្ឋាន',
        //     dataIndex: 'address',
        //     key: 'address',
        //     width: 250,
        //     render: (text, record) => (
        //         <span>
        //             {(record.village !== "ក្រៅសៀមរាប") && record.village+", "}
        //             {record.commune !== "ក្រៅសៀមរាប" && record.commune+", "}
        //             {record.district !== "ក្រៅសៀមរាប" && record.district+", "}
        //             {record.province && record.province}
        //         </span>
        //     ),
        // },
        {
            title: 'កាលបរិច្ឆេទស្កែន',
            dataIndex: 'createdAt',
            key: 'createdAt',
            width: 150,
            render: (text, record) => (
                <span size="middle">
                    {moment(record?.createdAt).format("ថៃ្ងទីDD ខែMM ឆ្នាំYYYY") } ៖ ម៉ោង{getKhmerTime(record?.createdAt)}
                </span>
            ),
        },
        {
            title: 'ទូរស័ព្ទ',
            dataIndex: 'tel',
            key: 'tel',
            width: 150,
            render: (text, record) => (
                <span size="middle">
                    {
                        record?.tel
                    }
                </span>
            ),
        },

        {
            title: 'ស្ថានភាព',
            dataIndex: 'status',
            key: 'status',
            width: 60,
            render: (text, record) => {
                let state ="អវិជ្ជមាន";
                let classN = "badge-success"
                let c_state = record?.currentState;

                if(c_state.confirm===true){
                    state = "វិជ្ជមាន"
                    classN= "badge-purple"
                }

                if(c_state.recovered===true){
                    state = "ជាសះស្បើយ"
                    classN= "badge-lightgreen"
                }

                if(c_state.death===true){
                    state = "ស្លាប់"
                    classN= "badge-error"
                }

                return (
                    <span className={classN}>{state}</span>
                )
            }
        },

        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 100,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    {
                       record?.id!==null ?
                       <Link className="link" to={"/subBuyer/" + record?.id}><EyeOutlined  /></Link>
                       :null
                    }
                    
                    {/* <Popconfirm
                        title="តើអ្នកពិតចង់លុបមែនឬទេ?"
                        onConfirm={() => { handleDelete(record.id) }}
                        okText="ចង់"
                        cancelText="មិនចង់"
                    >
                        <span className="link" style={{ color: "red" }}><DeleteOutlined /></span>
                    </Popconfirm> */}

                </Space>
            ),
        }
    ]
    return array
}